\*------------------------------------*/
/*------------------------------------*\
    FONT SIZE
    
    This mixin will generate a REM unit for your chosen font size. It will also add
    the usual pixel unit for a fallback for browsers that don't support REMS. 
    
    This can also caluculate a line height for you based on your base line-height ratio.
    
    You can append an !important too.
    
    You can override the base line-height ratio if needed too. 
    
    Usage example: 
    
    With line-height: 
    
    body {
    
    	@include font-size(16px, true);
    }
    
    Without line-height: 
    
    body {
    	
    	@include font-size(16px);
    }
    
\*------------------------------------*/
/*------------------------------------*\
    FORM FIELD
    
    Some useful base form field styles.   
\*------------------------------------*/
/*------------------------------------*\
    HOVER
    
    A useful little mixin to help generate better hover / focus etc. states.
    This also has an 'include-active' flag which will help account for active states too.  
\*------------------------------------*/
/*------------------------------------*\
    MEDIA QUERY

    This mixin will wrap your code in the appropriate media query of your choosing.

    For example. If you want to make the body background magenta on mobile only:

    body {
	    @include media-query(palm) {
	    	background: magenta;
	    }
    }
\*------------------------------------*/
/*------------------------------------*\
    MODE
    
    This simple mixin allows you to fence css into particular sections.
    For example: you may have an admin area. The admin area uses the global
    styles but also has its own. You'd create a new file in your 'project'
    directory called 'admin.scss'. In there you would set '$mode: "admin"'. 

    Within your components etc. you would then do the following: 

    .a-css-class {
    	
    	// This stuff will only render in admin.css
    	@include mode("admin") {
			background: red;
    	}
    }
\*------------------------------------*/
/*------------------------------------*\
    NO JS

    A simple mixin that will allow consistent no JS related styles 
\*------------------------------------*/
/*------------------------------------*\
    PLACEHOLDER
    
    This little mixin will help style those pesky placeholders    
\*------------------------------------*/
/*------------------------------------*\
    PRINT
    
    A very simply "wrapper" that allows you to add print specific styles within your css. 
    This is mainly in a mixin so that if anything changes in the future, we can just update this 
    mixin rather than refactoring our whole project.
    
    Usage example: 
    
    body {
    	color: #f3f3f3;
    
		@include print {
			color: #000000;
		}
    }
\*------------------------------------*/
