/*------------------------------------*\
    GRID WRAPPER
\*------------------------------------*/
.form, .grid, .site-head__inner, .site-foot__inner {
  display: grid;
  margin-left: -20px;
  margin-right: -20px;
  padding: 0 20px;
  grid-template-columns: repeat(10, 1fr); }

/*------------------------------------*\
    GRID ITEM
\*------------------------------------*/
.site-head__inner > * {
  display: flex;
  justify-content: center;
  align-items: center; }

.site-foot__column {
  display: flex;
  justify-content: flex-start;
  align-items: center; }

/*------------------------------------*\
    GRID COLUMNS
\*------------------------------------*/
.form, .grid--halves {
  grid-template-columns: repeat(12, 1fr); }
  .form > *, .grid--halves > * {
    grid-column: span 6; }
    @media screen and (max-width: 999px) {
      .form > *, .grid--halves > * {
        grid-column: span 12; } }

.site-head__inner {
  grid-template-columns: repeat(12, 1fr); }
  .site-head__inner > * {
    grid-column: span 4; }

.site-foot__inner {
  grid-template-columns: repeat(12, 1fr); }
  .site-foot__inner > * {
    grid-column: span 3; }

.grid--fifths {
  grid-template-columns: repeat(5, 1fr); }

